<template>
  <!-- 关注 -->
  <div class="follow">
    <div
      class="follow-item"
      @click="goDetail(item.item_id)"
      v-for="(item, index) in followData"
      :key="index"
    >
      <div class="nav" v-if="item.u">
        <div class="left" @click.stop="goUserDetail(item.u.id)">
          <div class="headImg">
            <img class="auto-img" :src="item.u.p" alt="" />
          </div>
          <div class="userInfo">
            <div class="top">
              <div class="name">{{ item.u.n }}</div>
              <div class="lv">lv{{ item.u.lv }}</div>
            </div>
            <div class="bottom">
              {{ item.u.followers_count_text }} ·
              {{ item.u.recipes_count_text }}
            </div>
          </div>
        </div>
        <div class="right interest" @click.stop="interest(index)">关注</div>
      </div>
      <div class="banner">
        <img class="auto-img" :src="item.img" alt="" />
      </div>
      <div class="collect-box">
        <div class="left">
          <div
            class="userimg"
            v-for="(user, index) in item.collect_users"
            :key="index"
          >
            <div class="item userimg">
              <img class="auto-img" v-show="user.p" :src="user.p" alt="" />
            </div>
          </div>
          <div class="usercount">{{ item.collect_count_text }}</div>
        </div>
        <div class="right">
          <div class="collect" @click.stop="goLogin(item.item_id, index)">
            <img
              class="auto-img collect-img"
              src="../assets/icon/collect.png"
              alt=""
            />
          </div>
          <div class="notes">
            <img class="auto-img" src="../assets/guanzhu/pinglun.png" alt="" />
          </div>
          <div class="share">
            <img class="auto-img" src="../assets/guanzhu/fenxiang.png" alt="" />
          </div>
        </div>
      </div>
      <div class="title">{{ item.trim_title }}</div>
    </div>
    <van-list
      offset="100"
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <br />
    </van-list>
  </div>
</template>

<script>
import { Toast } from 'vant'
import { Dialog } from 'vant'
export default {
  name: 'Follow',
  data() {
    return {
      followData: [],
      offset: 0,
      limit: 10,
      loading: false,
      finished: false,
      isCollect: false,
      isinterest: false,
      img: require('../assets/icon/xiaolian.png'),
    }
  },
  created() {
    this.getFollowData()
  },
  methods: {
    onLoad() {
      this.offset = this.offset + 10
      setTimeout(() => {
        this.axios({
          method: 'get',
          url:
            'https://apis.netstart.cn/douguo/home/ffeeds/' +
            this.offset +
            '/' +
            this.limit,
        })
          .then((result) => {
            console.log(a);
            if (result.status === 200) {
              this.followData.push(result.data.result.rfs)
            }
          })
          .catch((err) => {})
        this.loading = false
      }, 1000)
    },

    getFollowData() {
      this.axios({
        method: 'get',
        url:
          'https://apis.netstart.cn/douguo/home/ffeeds/' +
          this.offset +
          '/' +
          this.limit,
      })
        .then((result) => {
          if (result.status === 200) {
             ('result ==> ', result)
            this.followData = result.data.result.rfs
          }
        })
        .catch((err) => {})
    },

    goDetail(id) {
      this.$router.push({ name: 'Detail', params: { id } })
    },

    goLogin(id, index) {
      let collectImg = document.querySelectorAll('.collect-img')
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (this.isCollect) {
           (this.isCollect)
          this.isCollect = false
          collectImg[index].src = require('../assets/icon/collect.png')
          Toast('取消收藏')
          for (var i = 0; i < this.$root.scjl.length; i++) {
            if (id === this.$root.scjl[i]) {
              this.$root.scjl.splice(i, 1)
            }
          }
        } else {
           (this.isCollect)
           (id)
          this.isCollect = true
          collectImg[index].src = require('../assets/icon/liangwujiaoxing.png')
          Toast('收藏成功')
          for (var i = 0; i <= this.$root.scjl.length; i++) {
            if (id != this.$root.scjl[i]) {
              this.$root.scjl.push(id)
              break
            } else {
              if (id === this.$root.scjl[i]) {
                this.$root.scjl.splice(i, 1)
              }
            }
          }
        }
      }
    },

    interest(index) {
      let interest = document.querySelectorAll('.interest')

      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (!this.isinterest) {
          interest[index].innerText = '已关注'
          interest[index].style.backgroundColor = '#f1f1f1'
          this.isinterest = true
        } else {
          Dialog.confirm({
            title: '提示',
            message: '确定要取消关注ta吗?',
          }).then(() => {
            interest[index].innerText = '关注'
            interest[index].style.backgroundColor = '#facc30'
          })
          this.isinterest = false
        }
      }
    },

    goUserDetail(id) {
      this.$router.push({ name: 'Userdetail', params: { id } })
    },
  },
}
</script>

<style lang="less" scoped>
.follow {
  padding-top: 100px;
  padding-bottom: 50px;
  .nav {
    padding: 10px;
    display: flex;
    align-items: center;
    .left {
      display: flex;
      align-items: center;
      .headImg {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        overflow: hidden;
      }
      .userInfo {
        margin-left: 10px;
        .top {
          display: flex;
          .name {
            font-weight: bold;
            font-size: 14px;
          }
          .lv {
            margin-left: 10px;
            font-size: 14px;
            font-style: italic;
            color: #facc30;
          }
        }
        .bottom {
          color: #999;
          font-size: 12px;
        }
      }
    }
    .right {
      margin-left: auto;
      width: 60px;
      height: 30px;
      background-color: #facc30;
      border-radius: 15px;
      line-height: 30px;
      text-align: center;
    }
  }
  .banner {
    width: 100%;
  }
  .collect-box {
    padding: 10px;
    display: flex;
    align-items: center;
    .left {
      display: flex;
      align-items: center;
      position: relative;
      .userimg {
        display: flex;
        align-items: center;
        .item {
          width: 20px;
          height: 20px;
          border-radius: 50%;
          overflow: hidden;
        }
      }
      .userimg:nth-child(1) {
        position: absolute;
        left: 0;
        z-index: 2;
      }
      .userimg:nth-child(2) {
        position: absolute;
        left: 13px;
        z-index: 3;
      }
      .userimg:nth-child(3) {
        position: absolute;
        left: 26px;
        z-index: 4;
      }
      .userimg:nth-child(4) {
        position: absolute;
        left: 39px;
        z-index: 5;
      }
      .usercount {
        margin-left: 65px;
        font-size: 12px;
      }
    }
    .right {
      display: flex;
      margin-left: auto;
      .collect {
        width: 25px;
        height: 25px;
      }
      .notes {
        width: 25px;
        height: 25px;
        margin: 0 15px;
      }
      .share {
        width: 25px;
        height: 25px;
      }
    }
  }
  .title {
    padding: 0 10px 10px 10px;
    font-size: 14px;
  }
}
</style>
